<template>
  <q-layout view="LHh lpR lFf" class="bg-color">
    <q-page-container>
      <q-page padding class="container bg-color q-pb-xl flex">
        <div class="col-12 q-pb-lg fit self-start">
          <q-btn
            outline
            no-caps
            unelevated
            :ripple="{ color: 'accent' }"
            color="primary"
            class="rounded slide-hover button-gm-outline-dark text-body1 font-medium button-md"
            to="/"
          >
            <q-icon size="20px" name="icon-arrow-narrow-left" class="q-pr-xs" />
            Back to Growmodo
          </q-btn>
        </div>

        <div class="q-mx-auto">
          <div style="max-width: 696px">
            <div
              v-if="!request_submitted"
              class="text-h2 text-center font-medium text-primary q-pb-xl q-pt-sm letter-space-negative"
            >
              Request a {{ isTask ? 'Task' : 'Project' }}
            </div>
            <q-card
              bordered
              class="rounded shadows-sm full-width"
              :class="{
                ['card-padding-md']: $q.screen.lt.md,
                ['card-padding-lg']: !$q.screen.lt.md,
              }"
            >
              <q-form
                autocorrect="off"
                autocapitalize="off"
                autocomplete="off"
                spellcheck="false"
                @submit.prevent
              >
                <div class="row q-col-gutter-md">
                  <div v-if="request_submitted" class="col-12">
                    <!-- Done -->
                    <div class="text-center">
                      <CardIconBox icon="icon-check-circle-broken" />
                    </div>
                    <div
                      class="full-width font-medium text-h4 q-pt-lg text-center"
                    >
                      Thank you.<br />Your
                      {{ isTask ? 'task' : 'project' }} have been successfully
                      submitted.
                    </div>
                  </div>

                  <div v-else-if="showSkeleton" class="col-12">
                    <!-- Loading -->
                    <div class="row q-col-gutter-md">
                      <div class="col-12" v-for="j in [1, 2, 3, 4]" :key="j">
                        <q-skeleton
                          :width="`${j % 2 == 0 ? 45 : 35}%`"
                          type="QBadge"
                          class="q-my-md"
                        />
                        <q-skeleton type="QToolbar" width="100%" />
                      </div>
                    </div>
                  </div>

                  <template v-else>
                    <div
                      v-for="(question, i) in questions"
                      :key="question"
                      :name="i"
                      class="col-12"
                    >
                      <div class="row q-col-gutter-sm">
                        <div
                          class="col-12 text-body2 text-primary-500 font-medium"
                        >
                          {{ question.title }}
                        </div>
                        <div class="col-12">
                          <DynamicQuestions
                            :autofocus="false"
                            :question="question"
                            :initAnswer="answer[question.id]"
                            :platforms="question.options"
                            @answered="(ans) => (answer[question.id] = ans)"
                            :textareaRow="5"
                          ></DynamicQuestions>
                        </div>
                      </div>
                    </div>
                  </template>
                  <template v-if="!request_submitted">
                    <div class="col-12">
                      <div class="row items-center">
                        <q-icon
                          name="icon-alert-circle"
                          size="sm"
                          class="q-pr-sm"
                          color="warning-500"
                        />
                        Turnaround time might be longer if the brief is
                        incomplete.
                      </div>
                    </div>
                    <div class="col-12">
                      <div class="row items-center">
                        <q-btn
                          no-caps
                          unelevated
                          color="primary"
                          :disable="showSkeleton || request_submitting"
                          class="full-width text-body1 font-medium button-lg"
                          @click="submitRequest"
                        >
                          Submit Request
                          <q-icon
                            size="20px"
                            name="icon-arrow-narrow-right"
                            class="q-pl-xs"
                          />
                        </q-btn>
                      </div>
                    </div>
                  </template>
                  <template v-else>
                    <div class="col">
                      <q-btn
                        outline
                        no-caps
                        :ripple="{ color: 'accent' }"
                        color="primary"
                        class="rounded slide-hover full-width text-body1 font-medium button-gm-outline-dark button-lg"
                        @click="$root.goTo_Home"
                      >
                        <q-icon
                          size="20px"
                          name="icon-arrow-narrow-left"
                          class="q-pr-sm"
                        />
                        Back to home
                      </q-btn>
                    </div>
                    <div class="col">
                      <q-btn
                        no-caps
                        unelevated
                        :ripple="{ color: 'accent' }"
                        color="primary"
                        class="rounded slide-hover full-width text-body1 font-medium button-lg"
                        @click="goTo_Directory()"
                      >
                        Add your next request
                      </q-btn>
                    </div>
                  </template>
                </div>
              </q-form>
            </q-card>
          </div>
        </div>
      </q-page>
    </q-page-container>
  </q-layout>
</template>

<script>
import { defineComponent, defineAsyncComponent, ref } from 'vue';
import { mapState, mapActions } from 'pinia';
import useGrowmodo from 'stores/growmodo';
import useApp from 'stores/app';
import { extractErrorMessageString } from 'src/assets/scripts/functions';
import { checkIfArray } from 'src/assets/scripts/functions';

export default defineComponent({
  name: 'QuickRequest',

  components: {
    CardIconBox: defineAsyncComponent(() =>
      import('src/components/Helpers/CardIconBox.vue')
    ),
    DynamicQuestions: defineAsyncComponent(() =>
      import('src/components/DynamicQuestions.vue')
    ),
  },

  data() {
    return {
      request: {},
      questions: ref([]),
      answer: ref({}),
      request_submitting: false,
      request_submitted: false,
    };
  },

  computed: {
    ...mapState(useGrowmodo, ['taskDirectory', 'projectDirectory']),

    title() {
      return `Quick Request - ${this.isTask ? 'Task' : 'Project'}`;
    },

    type() {
      return this.$route.params?.quickRequestType || 'task';
    },

    isTask() {
      return this.type.toLowerCase() === 'task' ? true : false;
    },

    showSkeleton() {
      return !this.questions.length;
      // ||
      // Object.keys(this.answer).length !== this.questions.length
    },

    typeId() {
      const id = this.questions.findIndex(
        (e) =>
          e.type === (this.isTask ? 'select_tasktype' : 'select_projecttype')
      );
      return this.questions[id];
    },

    typeanswer() {
      return this.answer[this.typeId?.id];
    },

    requestType() {
      return this[this.isTask ? 'taskDirectory' : 'projectDirectory'][
        this.typeanswer?.value
      ];
    },
  },

  async mounted() {
    this.$root.setMeta({ title: this.title });
    const response = this.isTask ? await this.getTaskQuickRequest() : {};
    this.questions = response?.data?.dynamic_questions || [];
    await this.init_declare();
    this.init();
  },

  methods: {
    ...mapActions(useGrowmodo, [
      'getTaskQuickRequest',
      'requestTask',
      'requestProject',
    ]),
    ...mapActions(useApp, ['init_RequestForm']),

    async init_declare() {
      this.answer = {};
      this.request_submitting = false;
      this.request_submitted = false;
      this.uploads = {};
    },

    async submitRequest() {
      this.request_submitting = true;

      const payload = {
        title: `Quick Request - ${this.requestType?.title}`,
        [`${this.isTask ? 'task' : 'project'}_type_id`]: this.requestType?.id,
      };
      const dynamic_questions = [];
      for (const i of Object.keys(this.answer)) {
        dynamic_questions.push({
          id: i,
          answer: checkIfArray(this.answer[i])
            ? this.answer[i]
            : [this.answer[i]],
        });
      }
      payload.dynamic_questions = dynamic_questions;
      const response = this.isTask
        ? await this.requestTask(payload)
        : await this.requestProject(payload);
      const message = extractErrorMessageString(response?.message);
      if (response.success) {
        if (message)
          this.$q.notify({
            icon: 'icon-check-circle-broken',
            message: message,
          });
        this.request_submitted = true;
      } else {
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: message || 'Request submittion failed!',
        });
      }
      this.request_submitting = false;
    },

    async init() {
      this.init_RequestForm();
    },

    goTo_Directory(model) {
      if (!model) model = this.isTask ? 'task' : 'project';
      // Open Directory
      this.$root.toggleRequest(model == 'task' ? 'project' : 'task', false);
      this.$root.toggleRequest(model, true);
    },
  },
});
</script>
